.user-profile {
    max-width: 800px;
    margin: auto;
    font-family: Arial, sans-serif;
  }
  
  .profile-header {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-bottom: 30px;
  }
  
  .profile-picture img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
  }
  
  .profile-details h1 {
    margin: 0;
    font-size: 24px;
  }
  
  .profile-details p {
    margin: 5px 0;
    color: #555;
  }
  
  .create-post {
    margin-bottom: 30px;
  }
  
  .create-post textarea {
    width: 100%;
    margin-bottom: 10px;
  }
  
  .create-post input {
    display: block;
    margin-bottom: 10px;
  }
  
  .feed {
    margin-top: 20px;
  }
  
  .post {
    border: 1px solid #ddd;
    padding: 15px;
    margin-bottom: 20px;
    border-radius: 5px;
  }
  
  .post-images img {
    max-width: 100%;
    margin-bottom: 10px;
  }
  
  .timestamp {
    font-size: 0.8em;
  }
   
  